const ADD_MESSAGE = 'dialogReducer/ADD_MESSAGE';
// const UPDATE_TEXT_BOX_SYMBOLS_MESSAGE = 'UPDATE_TEXT_BOX_SYMBOLS_MESSAGE';

let initState = {
    dialogsData: [
        {id: 1, name: "Nikita"},
        {id: 2, name: "Alina"},
        {id: 3, name: "Lev"}
    ],
    messageData: [
        {
            id: 1,
            message: "Hi",
            avatar: 'http://cdn.forum.wotblitz.ru/wotb/ru/uploads/monthly_12_2018/post-74678117-0-31908900-1543839278.jpeg'
        },
        {
            id: 2,
            message: "How are You",
            avatar: 'http://cdn.forum.wotblitz.ru/wotb/ru/uploads/monthly_12_2018/post-74678117-0-31908900-1543839278.jpeg'
        },
        {
            id: 3,
            message: "Fine",
            avatar: 'http://cdn.forum.wotblitz.ru/wotb/ru/uploads/monthly_12_2018/post-74678117-0-31908900-1543839278.jpeg'
        }
    ],

    // newPostText: 'Omega-2000'
};

const dialogsReducer = (state = initState, action) => {
    switch (action.type) {
        case ADD_MESSAGE:{
            let newPost = {
                id: 5,
                message: action.postMessage,
                avatar: 'http://cdn.forum.wotblitz.ru/wotb/ru/uploads/monthly_12_2018/post-74678117-0-31908900-1543839278.jpeg'
            }
            return {
                ...state,
                // newPostText: '',
                messageData: [...state.messageData, newPost]
            }
        }
        // case UPDATE_TEXT_BOX_SYMBOLS_MESSAGE:{
        //     return {
        //         ...state,
        //         newPostText: action.postMessage
        //     }
        // }

        default:
            return state;
    }
}

export const addMessageActionCreator = (messageText) => {
    return {
        type: ADD_MESSAGE,
        postMessage: messageText
    }
}

// export const onMessageChangeActionCreator = (Text) => {
//     return {
//         type: UPDATE_TEXT_BOX_SYMBOLS_MESSAGE,
//         postMessage: Text
//     }
// }

export default dialogsReducer;