import {profileAPI} from "../api/api";

const ADD_POST = 'profileReducer/ADD-POST';
const DELETE_POST = 'profileReducer/DELETE-POST'
// const UPDATE_TEXT_BOX_SYMBOLS = 'UPDATE-TEXT-BOX-SYMBOLS';
const SET_USER_PROFILE = 'profileReducer/SET_USER_PROFILE';
const GET_USER_STATUS = 'profileReducer/GET_USER_STATUS';
const SET_USER_STATUS = 'profileReducer/SET_USER_STATUS';

let initState = {
    postsData: [
        {id: 1, message: 'Hellow', likescount: 50},
        {id: 2, message: 'Its me', likescount: 0},
        {id: 3, message: 'La ti tu da ti da da du ti du du di', likescount: 1220}
    ],

    // newPostText: 'Omega',
    lastId: 3,
    profile: null,
    status: null
};

const profileReducer = (state = initState, action) => {
    switch (action.type) {
        case DELETE_POST: {
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id != action.postId)
            }
        }
        case ADD_POST: {
            let newPost = {
                id: state.lastId + 1,
                message: action.postMessage,
                likescount: 0
            }
            return {
                ...state,
                lastId: state.lastId + 1,
                newPostText: '',
                postsData: [...state.postsData, newPost]
            }
        }
        // case UPDATE_TEXT_BOX_SYMBOLS: {
        //     return {
        //         ...state,
        //         newPostText: action.postMessage,
        //     }
        // }

        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }

        case GET_USER_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        case SET_USER_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        default:
            return state;
    }
}

export const addPostActionCreator = (postMessage) => {
    return {
        type: ADD_POST,
        postMessage: postMessage
    }
}

export const deletePostActionCreator = (postId) => {
    return {
        type: DELETE_POST,
        postId: postId
    }
}

// export const onPostChangeActionCreator = (Text) => {
//     return {
//         type: UPDATE_TEXT_BOX_SYMBOLS,
//         postMessage: Text
//     }
// }

export const setUserProfile = (profile) => {
    return {
        type: SET_USER_PROFILE,
        profile
    }
}

export const getUserStatus = (statusText) => {
    return {
        type: GET_USER_STATUS,
        status: statusText
    }
}

export const setUserStatus = (status) => {
    return {
        type: SET_USER_STATUS,
        status: status
    }
}

export const getUserStatusThunkCreator = (userId) =>
    async (dispatch) => {
        let response = await profileAPI.getUserStatus(userId)
        dispatch(getUserStatus(response.data))
    }

export const setUserStatusThunkCreator = (status) =>
    async (dispatch) => {
        let response = await profileAPI.setStatus(status)
        if (response.data.resultCode === 0) (
            dispatch(setUserStatus(status))
        )
    }

export const getUserProfileThunkCreator = (userId) =>
    async (dispatch) => {
        let response = await profileAPI.getUserProfile(userId)
        dispatch(setUserProfile(response.data));
    }

export default profileReducer;