import React from "react";
import formControlCSS from "./FormControl.module.css";
import {Field} from "redux-form";

const FormControl = ({input, meta: {touched, error}, child, children, ...props}) => {
    const errorCause = touched && error;
    return (
        <div className={formControlCSS.formControl + " " + (errorCause ? formControlCSS.error : '')}>
            <div>
                {children}
            </div>
            {errorCause && <span>{error}</span>}
        </div>
    )
}

export const TextArea = (props) => {
    const {input, meta, ...restProps} = props
    return (
        <FormControl {...props}><textarea  {...input} {...restProps}/></FormControl>
    )
}

export const CustomInput = (props) => {
    const {input, meta, ...restProps} = props
    return (
        <FormControl {...props}><input  {...input} {...restProps}/></FormControl>
    )
}

export const createField = (placeholder, name, validators, component, type =  "", text = "") =>
    <div>
        <Field placeholder={placeholder}
               name={name}
               component={component}
               validate={validators}
               type={type}
        /> {text}
    </div>
