import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'authReducer/SET_USER_DATA';
const UPDATE_USERNAME_BOX_SYMBOLS = 'authReducer/UPDATE-USERNAME-BOX-SYMBOLS';
const UPDATE_PASSWORD_BOX_SYMBOLS = 'authReducer/UPDATE-PASSWORD-BOX-SYMBOLS';
const UNSET_USER_DATA = 'authReducer/UNSET-USER-DATA';

let initState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    userNameText: '',
    passText: ''
};

const authReducer = (state = initState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data,
                isAuth: true
            }

        case UNSET_USER_DATA:
            return {
                ...state,
                userId: null,
                email: null,
                login: null,
                isAuth: false
            }
        // case UPDATE_USERNAME_BOX_SYMBOLS:
        //     return {
        //         ...state,
        //         userNameText: action.userNameText
        //     }
        //
        // case UPDATE_PASSWORD_BOX_SYMBOLS:
        //     return {
        //         ...state,
        //         passText: action.passText
        //     }

        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login) => {
    return {
        type: SET_USER_DATA,
        data: {userId, email, login}
    }
}

// export const setUserNameText = (userNameText) => {
//     return {
//         type: UPDATE_USERNAME_BOX_SYMBOLS,
//         userNameText: userNameText
//     }
// }

// export const setPasswordText = (passText) => {
//     return {
//         type: UPDATE_PASSWORD_BOX_SYMBOLS,
//         passText: passText
//     }
// }

export const unsetUserData = () => {
    return {
        type: UNSET_USER_DATA
    }
}

export const authMeThunkCreator = (id, email, login) =>
    async (dispatch) => {
        let response = await authAPI.authMe();
        if (response.data.resultCode === 0) {
            let {id, login, email} = response.data.data;
            dispatch(setAuthUserData(id, email, login));
        }
    }

export const authLoginThunkCreator = (email, password, rememberMe = false) =>
    async (dispatch) => {
        let response = await authAPI.authLogin(email, password, rememberMe);
        if (response.data.resultCode === 0) {
            dispatch(authMeThunkCreator())
        } else {
            let errorMessage = response.data.messages.length > 0 ? response.data.messages[0] : 'Unknown Error';
            let action = stopSubmit('login', {_error: errorMessage});
            dispatch(action);
        }
    }

export const authLogoutThunkCreator = () =>
    async (dispatch) => {
        let response = await authAPI.authLogout();
        if (response.data.resultCode === 0) {
            dispatch(unsetUserData())
        }
    }


export default authReducer;