import React from "react";
import friendsCSS from './friendsBar.module.css';
import FriendBarItem from "./FriendBarItem/friendBarItem";
// import Post from "../../Content/Profile/MyPosts/Post/post";

const FriendsBar = (props) => {

    let friendsHTML =
        props.state.map(friendBarItem => {
                return (<FriendBarItem key={friendBarItem.id} id={friendBarItem.id} name={friendBarItem.name} avatar={friendBarItem.avatar}/>);
            }
        );

    return (
        <div className={friendsCSS.friendsBar}>
            {friendsHTML}
        </div>
    )
}

export default FriendsBar;