import {followAPI, usersAPI} from "../api/api";
import {updateObjectInArray} from "../utils/objects-helpers";

const FOLLOW = 'usersReducer/FOLLOW';
const UNFOLLOW = 'usersReducer/UNFOLLOW';
const SET_USERS = 'usersReducer/SET_USERS';
const SET_CURRENT_PAGE = 'usersReducer/SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'usersReducer/SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'usersReducer/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'usersReducer/TOGGLE_IS_FOLLOWING_PROGRESS';

let initState = {
    users: [],
    pageSize: 100,
    totalUsersCount: 10,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
};

const followingState = (state, followed, userId) => {
    return state.users.map(u => {
            if (u.id === userId) {
                return {...u, followed: followed}
            }
            return u;
        }
    )
}

const usersReducer = (state = initState, action) => {
    switch (action.type) {
        case FOLLOW: {
            return {
                ...state,
                users: updateObjectInArray(state.users,
                    action.userId,
                    "id",
                    {followed: true}
                )
            }
        }

        case UNFOLLOW: {
            return {
                ...state,
                users: updateObjectInArray(state.users,
                    action.userId,
                    "id",
                    {followed: false})
            }
        }

        case
        SET_USERS: {
            return {...state, users: [...action.users]}
        }

        case
        SET_CURRENT_PAGE: {
            return {...state, currentPage: action.currentPage}
        }

        case
        SET_TOTAL_USERS_COUNT: {
            return {...state, totalUsersCount: action.usersCount}
        }

        case
        TOGGLE_IS_FETCHING: {
            return {...state, isFetching: action.isFetching}
        }

        case
        TOGGLE_IS_FOLLOWING_PROGRESS: {
            return {
                ...state, followingInProgress: action.isFetching ?
                    [...state.followingInProgress, action.userId]
                    : [...state.followingInProgress.filter(id => id !== action.userId)]
            }
        }

        default: {
            return state;
        }
    }
}

export const follow = (userId) => (
    {type: FOLLOW, userId}
)

export const unfollow = (userId) => (
    {type: UNFOLLOW, userId}
)

export const setUsers = (users) => (
    {type: SET_USERS, users}
)

export const setCurrentPage = (currentPage) => (
    {type: SET_CURRENT_PAGE, currentPage}
)

export const setTotalUsersCount = (usersCount) => (
    {type: SET_TOTAL_USERS_COUNT, usersCount}
)

export const toggleIsFetching = (isFetching) => (
    {type: TOGGLE_IS_FETCHING, isFetching}
)

export const toggleIsFollowingProgress = (isFetching, userId) => (
    {type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId}
)


