export const getUsersThunkCreator = (currentPage, pageSize) =>
    async (dispatch) => {
        dispatch(toggleIsFetching(true));
        let data = await usersAPI.getUsers(currentPage, pageSize)
        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    }

const following = async (dispatch, followingApiFunction, followingFunction, userId) => {
    dispatch(toggleIsFollowingProgress(true, userId))
    let response = await followingApiFunction(userId)
    if (response.data.resultCode === 0) {
        dispatch(followingFunction(userId))
    }
    dispatch(toggleIsFollowingProgress(false, userId))
}


export const followThunkCreator = (userId) => {
    return (dispatch) => {
        following(dispatch, followAPI.follow, follow, userId)
    }
}

export const unFollowThunkCreator = (userId) => {
    return (dispatch) => {
        following(dispatch, followAPI.unfollow, unfollow, userId)
    }
}

