import {authMeThunkCreator} from "./authReducer";

const INIT_SUCCESS = "appReducer/INIT_SUCCESS";
// const INIT_OFF = "INIT_OFF";

let initState = {
    initialized: false
}

export const appReducer = (state = initState, action) => {
    switch (action.type) {
        case INIT_SUCCESS: {
            return {
                ...state,
                initialized: true
            }
        }

        default:
            return state;
    }
}

export const initializedSuccessAC = () => ({type: INIT_SUCCESS})

export const initializeAppTC = () => (dispatch) => {
    let promise = dispatch(authMeThunkCreator());
    promise.then(() =>
        dispatch(initializedSuccessAC())
    )
}