import React from "react";
import {Field, reduxForm} from "redux-form";
import {createField, CustomInput} from "../Common/FormControls/FormControls";
import {maxLengthCreator, requiredField} from "../../utils/validators/validators";
import formControlCSS from "./../Common/FormControls/FormControl.module.css";

let maxLength = maxLengthCreator(50);

const LoginForm = ({handleSubmit, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField("Login", "login", [requiredField, maxLength], CustomInput)}
            {createField("Password", "password", [requiredField, maxLength], CustomInput, "password")}
            {createField(null, "rememberMe", [], CustomInput, "checkbox", "Remember Me")}
            {error && <div className={formControlCSS.formSummaryError}>{error}</div>}
            <div>
                <button>Войти</button>
            </div>
        </form>
    )
}

const ReduxLoginForm = reduxForm({
    form: 'login'
})(LoginForm)

const Login = ({isAuth, authLogout, authLogin, login}) => {

    const onLoginButtonClick = (formData) => {
        console.log(formData);
        let userNameText = formData.login;
        let passwordText = formData.password;
        let rememberMe = formData.rememberMe ? formData.rememberMe : false;
        authLogin(userNameText, passwordText, rememberMe);
    }

    const onLogoutButtonClick = () => {
        authLogout();
    }

    return (
        <div>
            {
                isAuth ? <div>
                        <div onClick={onLogoutButtonClick}>Выйти</div>
                        <div>{login}</div>
                    </div> :
                    <ReduxLoginForm onSubmit={onLoginButtonClick}/>
            }
        </div>
    )
}

export default Login;