import friendBarItemCSS from "./friendBarItem.module.css";
import React from "react";
import {NavLink} from 'react-router-dom';
// import navcss from "../../nav.module.css";

const FriendBarItem = (props) => {
    return (
        <div className={friendBarItemCSS.friendBarItem}>
            <NavLink to={'/friends/'+props.id}>
                <div className={friendBarItemCSS.avatar}><img alt={'Avatar'} src={props.avatar}/></div>
                <div className={friendBarItemCSS.name}>{props.name}</div>
            </NavLink>
        </div>
    )
}

export default FriendBarItem;