import './App.css';
import {BrowserRouter, Route, withRouter} from 'react-router-dom';
import React, {Suspense} from "react";
import Logo from "./components/Logo/logo";
import Nav from "./components/Nav/nav";
import Footer from "./components/Content/Footer/footer.js";
import HeaderContainer from "./components/Header/headerContainer";
import LoginContainer from "./components/Login/loginContainer";
import Test from "./Temp/Subscriber";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeAppTC} from "./Redux/appReducer";
import Preloader from "./components/Common/Preloader/preloader";
import store from "./Redux/reduxStore";
import {withSuspenseComponent} from "./HOC/withSuspenseFallBack";

const DialogsContainer = React.lazy(() => import("./components/Content/Dialogs/dialogsContainer"));
const UsersContainer = React.lazy(() => import("./components/Content/Users/usersContainer"));
const ProfileContainer = React.lazy(() => import("./components/Content/Profile/profileContainer"));
const Friends = React.lazy(() => import("./components/Content/Friends/friends"));
const Music = React.lazy(() => import("./components/Content/Music/music"));
const Settings = React.lazy(() => import("./components/Content/Settings/settings"));


class App extends React.Component {
    componentDidMount() {
        this.props.initializeApp();
    }

    render() {
        console.log(React.version);
        if (!this.props.initialized) {
            return <Preloader></Preloader>
        }
        return (

            <div className={'app-wrapper'}>
                <Logo/>
                <HeaderContainer/>
                <Nav
                    state={this.props.appState.navBar}
                />
                <div className={'app-wrapper-content'}>
                    <Route path="/profile/:userId?"
                           render={withSuspenseComponent(ProfileContainer)}
                    />
                    <Route path="/messages"
                           render={withSuspenseComponent(DialogsContainer)}
                    />
                    <Route path="/music" component={withSuspenseComponent(Music)}/>
                    <Route path="/friends" component={withSuspenseComponent(Friends)}/>
                    <Route path="/users" component={withSuspenseComponent(UsersContainer)}/>
                    <Route path="/settings" component={withSuspenseComponent(Settings)}/>
                    <Route path="/login" component={() => <LoginContainer/>}/>
                    <Route path="/test" component={() => <Test/>}/>
                </div>
                <Footer/>
            </div>
        )
            ;
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})

const AppContainerWithRouter = compose(withRouter, connect(mapStateToProps, {initializeApp: initializeAppTC}))(App);

export const MainApp = () => {
    return (
        <BrowserRouter>
            <Provider store={store}>
                <AppContainerWithRouter appState={store.getState()}/>
            </Provider>
        </BrowserRouter>
    )
}