import React from "react";
import navcss from './nav.module.css';
import FriendsBar from "./Friends/friendsBar";
import {NavLink} from 'react-router-dom';
// import Friends from "../Content/Friends/friends";

const Nav = (props) => {
    return (
        <nav className={navcss.nav}>
            <div className={`${navcss.item} ${navcss.active}`}>
                <NavLink to={"/profile"} activeClassName={navcss.active}>Profile</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/messages"} activeClassName={navcss.active}>Messages</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/music"} activeClassName={navcss.active}>Music</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/friends"} activeClassName={navcss.active}>Friends</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/users"} activeClassName={navcss.active}>Users</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/settings"} activeClassName={navcss.active}>Settings</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/test"} activeClassName={navcss.active}>Test</NavLink>
            </div>
            <div className={navcss.item}>
                <NavLink to={"/login"} activeClassName={navcss.active}>Test</NavLink>
            </div>
            Friends:
            <FriendsBar state={props.state.friendsBarData}/>
        </nav>
    );
}

export default Nav;